import flyio from 'flyio';

function proxyAjax(method, ...arg) {
  return new Promise(function(reslove, reject) {
    flyio[method](...arg).then(function(json) {
      if (json.code) {
        reject(json);
      } else {
        reslove(json);
      }
    }, function(json) {
      if(json){
        reject(json)
      }
    });
  });
}

function post() {
  return proxyAjax('post', ...arguments);
};

function get() {
  return proxyAjax('get', ...arguments);
};

export {
  post,
  get,
};



