import Vue from 'vue';
import Router from 'vue-router';

const home = () => import('../module/home.vue');
const api = () => import('../module/apiList/api.vue');
const addStore = () => import('../module/setting/addStore.vue');
const storeList = () => import('../module/setting/list.vue');
const store = () => import('../module/setting/store.vue');
Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'mock',
      meta: {level: 'a1', title: 'home'},
      component: home,
      children: [
        {
          path: '/api/:store?',
          name: 'api',
          meta: {level: 'b1', title: '接口列表'},
          component: api,
        },
        {
          path: '/setting/addStore',
          name: 'addStore',
          meta: {level: 'b2', title: '添加仓库'},
          component: addStore,
        },
        {
          path: '/setting/storeList',
          name: 'storeList',
          meta: {level: 'b3', title: '仓库列表'},
          component: storeList,
        },
        {
          path: '/setting/:store?',
          name: 'store',
          meta: {level: 'b4', title: '修改仓库'},
          component: store,
          props: true
        },
        {path: '/**', redirect: '/api'},
      ],
    },
    {path: '/**', redirect: '/'},
  ],
});



