/**
 * Created by liux on 18/3/21.
 * @param get name ,set name value ,clear name
 * @return value or undefined
 */
var hasLocal = true;
var storage = window.localStorage;

if (storage) {
  try {
    storage.setItem('_localStorageTest', 'Hello World!');
    storage.removeItem('_localStorageTest');
  } catch (e) {
    hasLocal = false;
  }
} else {
  hasLocal = false;
}

function getLocal(name) {
  if(!hasLocal){
    return ''
  }
  return JSON.parse(storage.getItem(name));
};
function setLocal(name, val) {
  if(!hasLocal){
    return ''
  }
  storage.setItem(name, JSON.stringify(val));
};
function clearLocal(name) {
  if(!hasLocal){
    return ''
  }
  setLocal(name, '');
}

export {
  getLocal,
  setLocal,
  clearLocal,
};


