
import {getLocal, setLocal, clearLocal} from 'common/helper/local'
import {post } from 'common/network/http'

export default {
  state: {
    currentStore:{},
    addFlag: false,
    storeList:[]
  },
  mutations: {
    currentStore(state, payload){
      state.currentStore = payload
    },
    addFlag(state, payload) {
      state.addFlag = payload;
    },
    storeList(state,payload){
      state.storeList = payload
    },
  },
  actions:{
    getStoreList({commit}){
      return post('/setting/queryStore').then(json=>{
        var storeList = json.data.map(item=>{
          item.newStore = item.user;
          item.store = item.user;
          item.newUser = item.user;
          return item
        })
        commit('storeList',storeList)
        return json
      })
    },
  },
};



